// WordPress API utilities
const WORDPRESS_API_URL = process.env.WORDPRESS_API_URL || 'http://agro-rajaguru.local/wp-json/wp/v2/';

export interface HeaderData {
  logo: {
    url: string;
    alt: string;
  };
  navigation: Array<{
    acf_fc_layout: string;
    menu_item_name: string;
    menu_item_link: {
      title: string;
      url: string;
      target: string;
    };
  }>;
  button_text: string;
  button_link: {
    title: string;
    url: string;
    target: string;
  };
}

export interface WordPressHeaderResponse {
  id: number;
  date: string;
  date_gmt: string;
  guid: {
    rendered: string;
  };
  modified: string;
  modified_gmt: string;
  slug: string;
  status: string;
  type: string;
  link: string;
  title: {
    rendered: string;
  };
  content: {
    rendered: string;
    protected: boolean;
  };
  featured_media: number;
  template: string;
  meta: {
    _acf_changed: boolean;
  };
  class_list: string[];
  acf: {
    logo: {
      ID: number;
      id: number;
      title: string;
      filename: string;
      filesize: number;
      url: string;
      link: string;
      alt: string;
      author: string;
      description: string;
      caption: string;
      name: string;
      status: string;
      uploaded_to: number;
      date: string;
      modified: string;
      menu_order: number;
      mime_type: string;
      type: string;
      subtype: string;
      icon: string;
      width: number;
      height: number;
      sizes: {
        thumbnail: string;
        'thumbnail-width': number;
        'thumbnail-height': number;
        medium: string;
        'medium-width': number;
        'medium-height': number;
        medium_large: string;
        'medium_large-width': number;
        'medium_large-height': number;
        large: string;
        'large-width': number;
        'large-height': number;
        '1536x1536': string;
        '1536x1536-width': number;
        '1536x1536-height': number;
        '2048x2048': string;
        '2048x2048-width': number;
        '2048x2048-height': number;
      };
    };
    navigation: Array<{
      acf_fc_layout: string;
      menu_item_name: string;
      menu_item_link: {
        title: string;
        url: string;
        target: string;
      };
    }>;
    button_text: string;
    button_link: {
      title: string;
      url: string;
      target: string;
    };
  };
  _links: {
    self: Array<{
      href: string;
      targetHints: {
        allow: string[];
      };
    }>;
    collection: Array<{
      href: string;
    }>;
    about: Array<{
      href: string;
    }>;
    'wp:attachment': Array<{
      href: string;
    }>;
    curies: Array<{
      name: string;
      href: string;
      templated: boolean;
    }>;
  };
}

export async function fetchHeaderData(): Promise<HeaderData> {
  try {
    const response = await fetch(`${WORDPRESS_API_URL}header`, {
      next: { revalidate: 3600 }, // Revalidate every hour
    });

    if (!response.ok) {
      throw new Error(`Failed to fetch header data: ${response.status}`);
    }

    const data: WordPressHeaderResponse[] = await response.json();
    
    if (data.length === 0) {
      throw new Error('No header data found');
    }

    const headerData = data[0];
    
    return {
      logo: {
        url: headerData.acf.logo.url,
        alt: headerData.acf.logo.alt || 'Logo',
      },
      navigation: headerData.acf.navigation,
      button_text: headerData.acf.button_text,
      button_link: headerData.acf.button_link,
    };
  } catch (error) {
    console.error('Error fetching header data:', error);
    
    // Return the exact data structure from your provided endpoint
    return {
      logo: {
        url: 'http://agro-rajaguru.local/wp-content/uploads/2025/09/logo3.webp',
        alt: '',
      },
      navigation: [
        {
          acf_fc_layout: 'navigation_items',
          menu_item_name: 'Home',
          menu_item_link: { title: '', url: '#', target: '' },
        },
        {
          acf_fc_layout: 'navigation_items',
          menu_item_name: 'About us',
          menu_item_link: { title: '', url: '#', target: '' },
        },
        {
          acf_fc_layout: 'navigation_items',
          menu_item_name: 'Services',
          menu_item_link: { title: '', url: '#', target: '' },
        },
        {
          acf_fc_layout: 'navigation_items',
          menu_item_name: 'Our Team',
          menu_item_link: { title: '', url: '#', target: '' },
        },
        {
          acf_fc_layout: 'navigation_items',
          menu_item_name: 'Testimonials',
          menu_item_link: { title: '', url: '#', target: '' },
        },
        {
          acf_fc_layout: 'navigation_items',
          menu_item_name: 'Contact Us',
          menu_item_link: { title: '', url: '#', target: '' },
        },
      ],
      button_text: 'Get in touch',
      button_link: { title: '', url: '#', target: '' },
    };
  }
}
